@import url(https://fonts.googleapis.com/css2?family=PT+Serif:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  cursor: default;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App-header {
  align-items: center;
  background-color: white;
  display: flex;
  flex-direction: column;
  height: 100vh;
  padding-top: 32px;
  width: 90px;
}

h1 {
  color: #000000;
  font-family: 'PT Serif', serif;
  font-size: 39px;
  margin: 0;
  padding: 0;
  -webkit-text-orientation: mixed;
          text-orientation: mixed;
  transform: rotate(180deg);
  -ms-writing-mode: tb-rl;
      writing-mode: vertical-rl;
}
.container-canvas {
  background-color: #fff;
  padding: 20px 20px 20px 6px;
  width: auto;
  display: flex;
}

.canvas {
  width: calc(100vh - 40px);
}
* {
  box-sizing: border-box;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
li {
  margin: 0;
  padding: 0;
}

.App {
  display: flex;
  height: 100vh;
  width: 100%;
  overflow: hidden;
}
.App-body {
  display: flex;
  width: 100%;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}



.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.parametric-menu-container {
  background-color: white;
  padding: 20px;
  width: 100%;
}

h2 {
  font-family: 'PT Serif', serif;
  font-weight: bold;
  font-size: 42px;
  line-height: 125%;
  margin: 0;
  text-transform: capitalize;
}

.dropdown-container {
  position: relative;
  height: 90px;
  overflow: hidden;
  transition: height .3s ease;
  padding: 2rem;
}
.dropdown-container.visible {
  height: 475px;
}
.dropdown-container.visible .options-list {
  top: 90px;
}
.options-list {
  position: absolute;
  top: -385px;
  height: 0;
  margin: 0;
  padding: 0;
  list-style: none;
  transition: top 1s ease;
}

.option {
  font-family: 'PT Serif', serif;
  color: #b4bedb;
  margin: 0;
  font-size: 21px;
  line-height: 143%;
  text-transform: capitalize;
}

.option.selected {
  color: #000000;
  background: none;
}

.option:hover {
  color: #899ACA;
}
.color-selector-container {
  display: flex;
  overflow: hidden;
  padding: 2rem;
  position: relative;
}

.color-item {
  border-radius: 50%;
  height: 42px;
  width: 42px;
  margin-right: .8rem;
}

.color-item.selected,
.color-item:hover {
  border: 3px solid rgba(0, 0, 0, 0.1);
}

.separator {
  background-color: #e6eaf3;
  height: 42px;
  margin-right: 1rem;
  width: 2px;
}

.color-item.default {
  background: url(data:image/png;base64,) center/cover no-repeat;
}

.color-item.default:hover{
  border: none;
}

#picker {
  position: absolute;
  top: 70px;
  visibility: hidden;
}
.vectors-list {
  display: flex;
  flex-wrap: wrap;
  overflow-y: scroll;
}
.children-menu {
  display: flex;
  font-family: 'PT Serif', serif;
  font-size: 24px;
  font-weight: bold;
  padding: 1.2rem;
  width: 100%;
}
.child-item {
  color: #b4bedb;
  padding: 5px 14px;
}
.child-item:hover {
  background-color: rgba(230, 234, 243, 0.4);
  border-radius: 10px;
}
.child-item.selected {
  color: #000;
}
.item {
  overflow: hidden;
  height: 180px;
  width: 180px;
}

.item svg {
  border-radius: 1.25rem;
  fill: none;
  height: 180px;
  transform-origin: center center;
  width: 180px;
}

.item.selected,
.item:hover {
  border-radius: 1.25rem;
  background-color: rgba(230, 234, 243, 0.4);
}
.item.empty {
  border-radius: 1.25rem;
  height: 180px;
  width: 180px;  
}

svg.face {
  transform: scale(2) translateY(-14px);
}

svg.hairstyle {
  transform: scale(1.2) translateY(15px);
}

svg.fringe {
  transform: scale(2) translateY(15px);
}
svg.ears {
  transform: scale(7) translate(30px, 4px);
}
svg.pupil,
svg.narrow,
svg.normal,
svg.wide {
  transform: scale(7) translate(17px, 8px);
}
svg.eyebrows {
  transform: scale(5) translate(14px, 14px);
}
svg.glasses {
  transform: scale(4) translate(14px, 5px);
}
svg.nostril {
  transform: scale(7) translateY(-7px);
}
svg.septum {
  transform: scale(7) translate(7px, 2px);
}
svg.upper,
svg.smile,
svg.lower {
  transform: scale(6) translateY(-17px);
}

svg.head {
  transform: scale(3) translateY(20px);
}
svg.mouth {
  transform: scale(4) translate(20px, -10px);
}
svg.mustachio {
  transform: scale(2.2) translate(6px, -15px);
}
svg.beard {
  transform: scale(2.2) translateY(-18px);
}
